/*
    body filter style
 */

.body__filter {
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -5;
  transition: all 0.55s ease-in-out;
}

/*
    feedback style
 */

.feedback {
  box-sizing: border-box;
  width: 600px;
  padding: 23px 20px 0;
  background: linear-gradient(to bottom, #fff 343px, transparent 343px);
  position: relative;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(-90deg);
  z-index: -5;
  transition: all 0.55s ease-in-out;

  @include mq(md) {
    background: #fff;
    padding: 23px 20px 20px;
    height: auto;
    max-height: 90%;
    overflow-y: scroll;
  }

  @include mq(s) {
    width: 80%;
  }

  span.close {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;

    @include mq(xs) {
      top: 10px;
      right: 10px;
    }
  }

  p {
    font-family: $fb, sans-serif;
    font-size: 22px;
    color: #465626;
    text-align: center;
    text-transform: uppercase;
  }

  form {
    margin-top: 35px;

    .form-group {

      input {
        font-family: $fbk, sans-serif;
        font-size: 18px;
        color: #888;
        display: block;
        box-sizing: border-box;
        width: 45%;
        padding: 14.5px 20px;
        background: #e3dedd;
        border: none;
        margin-bottom: 20px;

        @include mq(s) {
          width: 60%;
          max-width: 250px;
        }

        @include mq(xs) {
          width: 100%;
          max-width: 100%;
        }

        &::placeholder {
          font-family: $fbk, sans-serif;
          font-size: 18px;
          color: #888;
          opacity: 1;
        }
      }

      textarea {
        font-family: $fbk, sans-serif;
        font-size: 18px;
        color: #888;
        display: block;
        box-sizing: border-box;
        width: 98%;
        padding: 10px 20px;
        background: #e3dedd;
        border: none;
        resize: none;

        @include mq(xs) {
          width: 100%;
        }

        &::placeholder {
          font-family: $fbk, sans-serif;
          font-size: 18px;
          color: #888;
          opacity: 1;
        }
      }

      button {
        font-family: $fb, sans-serif;
        font-size: 24px;
        color: #fff;
        display: block;
        box-sizing: border-box;
        width: 100%;
        max-width: 250px;
        margin: 57px auto 0;
        padding: 10px 0;
        background: #ffb144;
        border: 1px solid #ffb144;
        transition: all 0.55s ease-in-out;

        &:hover {
          color: #ffb144;
          background: transparent;
        }
      }
    }
  }
}