/*
    information style
 */

.information {
  padding: 91px 0 10px;

  @include mq(md) {
    padding: 41px 0 10px;
  }

  .container {
    display: flex;
    justify-content: space-between;
    position: relative;

    @include mq(s) {
      display: block;
    }
  }

  h4 {

    @include mq(s) {
      text-align: center;
    }
  }

  ul {
    position: relative;
    padding: 17px 0 25px;
    margin-top: 42px;

    li {
      margin-bottom: 48px;
      position: relative;

      span {
        font-family: $fd, sans-serif;
        font-size: 22px;
        color: #2a3411;
        margin-left: 90px;
        display: block;
        position: relative;
        z-index: 9;

        @include mq(s) {

          br {
            display: none;
          }
        }

        @include mq(xs) {
          margin-left: 80px;
        }
      }

     &:last-child {
        margin-bottom: 0;
      }

      &:before {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    &:before {
      content: "";
      display: block;
      width: 70px;
      height: 100%;
      background: #adc965;
      box-shadow: 0px 0px 9.5px 0.5px rgba(0, 0, 0, 0.1);
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  .why {
    width: 50%;

    @include mq(s) {
      width: 100%;
      box-sizing: border-box;
      padding-left: 7px;
    }

    @include mq(xs) {
      padding-left: 0;
    }

    h4 {
      margin-left: 27px;

      @include mq(md) {
        margin-left: 0;
      }
    }

    ul {
      margin-left: 59px;

      @include mq(md) {
        margin-left: 0;
      }

      li {

        &:nth-child(1) {

          &:before {
            width: 41px;
            height: 41px;
            background: url("../img/why/1.png") no-repeat center/cover;
            left: 15px;
          }
        }

        &:nth-child(2) {

          &:before {
            width: 42px;
            height: 41px;
            background: url("../img/why/2.png") no-repeat center/cover;
            left: 14px;
          }
        }

        &:nth-child(3) {

          &:before {
            width: 36px;
            height: 46px;
            background: url("../img/why/3.png") no-repeat center/cover;
            left: 17px;
          }
        }
      }
    }
  }

  .delivery {
    width: 50%;
    box-sizing: border-box;
    padding-left: 77px;

    @include mq(md) {
      padding-left: 27px;
    }

    @include mq(s) {
      width: 100%;
      padding-left: 7px;
      margin-top: 43px;
    }

    @include mq(xs) {
      padding-left: 0;
    }

    ul {

      li {

        &:nth-child(1) {

          &:before {
            width: 40px;
            height: 40px;
            background: url("../img/delivery/1.png") no-repeat center/cover;
            left: 14px;
          }
        }

        &:nth-child(2) {

          &:before {
            width: 42px;
            height: 42px;
            background: url("../img/delivery/2.png") no-repeat center/cover;
            left: 15px;
          }
        }

        &:nth-child(3) {

          &:before {
            width: 46px;
            height: 36px;
            background: url("../img/delivery/3.png") no-repeat center/cover;
            left: 14px;
          }
        }

        &:nth-child(4) {

          &:before {
            width: 39px;
            height: 38px;
            background: url("../img/delivery/4.png") no-repeat center/cover;
            left: 17px;
          }
        }
      }
    }
  }

  .leaf {
    top: -114px;
    left: -360px;
    z-index: 9;

    @include mq(xlg) {
      top: 160px;
      left: -320px;
    }

    @include mq(md) {
      left: -340px;
    }

    @include mq(s) {
      top: 68%;
    }

    @include mq(xs) {
      top: 75%;
      left: -365px;
    }
  }
}