/*
    gallery style
 */

.gallery {
  background: url("../img/advantages-bg.jpg") no-repeat center/cover;
  padding: 95px 0 15px;
  position: relative;

  @include mq(md) {
    padding: 55px 0 15px;
  }

  .container {
    width: 1300px;

    @include mq(xlg) {
      width: 1140px;
    }

    @include mq(lg) {
      width: 960px;
    }

    @include mq(md) {
      width: 720px;
    }

    @include mq(s) {
      width: 94%;
    }

    @include mq(xs) {
      width: 90.625%;
    }
  }

  h3 {
    text-align: center;
    max-width: 910px;
    margin: 0 auto;
    position: relative;
    z-index: 9;
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 25px 30px 0;
    background: #fff;
    box-shadow: 0px 0px 9.5px 0.5px rgba(0, 0, 0, 0.1);
    margin-top: 25px;
    position: relative;
    z-index: 9;

    @include mq(xs) {
      padding: 10px 10px 0;
    }

    a {
      box-sizing: border-box;
      width: 23%;
      max-width: 290px;
      position: relative;
      transition: all 0.55s ease-in-out;
      margin-bottom: 25px;

      @include mq(xs) {
        margin-bottom: 10px;
      }

      @include mq(s) {
        width: 48%;
        max-width: 320px;
      }

      img {
        width: 100%;
      }

      &:before {
        content: "";
        display: block;
        box-sizing: border-box;
        border: 8px solid rgba(255, 255, 255, .2);
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }

      &:after {
        content: "";
        display: block;
        width: 78px;
        height: 78px;
        background: url("../img/zoom.png") no-repeat center/cover;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: inherit;
      }

      &:hover {

        img {
          filter: blur(2px);
        }

        &:after {
          opacity: 1;
        }
      }
    }
  }

  .dandelions {
    width: 29%;
    top: -279px;
    left: 0;

    @include mq(s) {
      width: auto;
      top: -370px;
      left: -435px;
    }

    img {
      width: 100%;
    }
  }
}