/*
    about style
 */

.about {
  background: url("../img/advantages-bg.jpg") no-repeat center/cover;
  padding: 0 0 50px;
  position: relative;

  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 9;

    @include mq(md) {
      align-items: center;
    }

    @include mq(s) {
      flex-wrap: wrap;
    }

    &-text {
      box-sizing: border-box;
      margin-left: 26px;
      margin-top: 132px;
      width: 45%;

      @include mq(lg) {
        margin-top: 82px;
        width: 47%;
      }

      @include mq(md) {
        margin-top: 0;
        margin-left: 0;
        width: 45%;
      }

      @include mq(s) {
        width: 100%;
      }
      
      h3 {

        @include mq(s) {
          text-align: center;
        }
      }
      
      p {
        font-family: $fbk, sans-serif;
        font-size: 20px;
        color: #364118;
        max-width: 417px;
        margin-top: 20px;

        @include mq(s) {
          max-width: 100%;
        }
      }
    }

    &-img {
      width: 55%;

      @include mq(md) {
        width: 52%;
      }

      @include mq(s) {
        width: 100%;
      }

      img {
        position: relative;
        left: 15px;

        @include mq(xlg) {
          left: 0;
          width: 100%;
        }
      }
    }
  }

  .dandelions {
    width: 28%;
    max-width: 534px;
    position: absolute;
    top: -461px;
    left: 0;

    img {
      width: 100%;
    }
  }

  &:after {
    content: "";
    display: block;
    width: 100%;
    height: 20px;
    background: linear-gradient(to right, #89d5c9, #ff8357 10%, #adc965 20%, #89d5c9 30%, #ff8357 40%, #adc965 50%, #89d5c9 60%, #adc965 70%, #89d5c9 80%, #ff8357 90%, #adc965);;
    box-shadow: 0 -5px 13px 2px rgba(0, 0, 0, .15);
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
  }
}