/*
    description style
 */

.description {
  background: url("../img/advantages-bg.jpg") no-repeat center/cover;
  padding: 37px 0 17px;
  position: relative;

  @include mq(md) {
    padding: 0 0 35px;
  }

  h3 {
    width: 528px;
    margin-left: auto;
    margin-right: 50px;
    position: relative;
    z-index: 9;

    @include mq(md) {
      margin: 0 auto;
      width: 100%;
    }
  }

  figure {
    margin: -15px 0 0;
    position: relative;

    @include mq(lg) {
      width: 67%;
    }

    @include mq(md) {
      margin: 0 0;
      width: 68%;
    }

    @include mq(s) {
      width: 100%;
      margin: 10px auto 20px;
    }

    img {
      margin: 0;
      position: relative;
      z-index: 9;

      @include mq(lg) {
        width: 100%;
      }
    }

    &:before {
      content: "";
      display: block;
      width: 57%;
      height: 129%;
      background: #ff8357;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 160px;
      transform: translateY(-50%);
      filter: blur(100px);

      @include mq(md) {
        width: 100%;
        filter: blur(70px);
        left: 50%;
        transform: translate(-50%, -50%);
      }

      @include mq(xs) {
        filter: blur(50px);
      }
    }
  }

  &__content {
    margin-top: 60px;
    position: relative;
    z-index: 9;

    @include mq(s) {
      margin-top: 0;
    }

    p {
      font-family: $fbk, sans-serif;
      font-size: 18px;
      color: #364118;
      box-sizing: border-box;
      width: 273px;
      padding: 12px 10px 17px 13px;
      background: #fff;

      @include mq(s) {
        width: 75%;
        margin: 0 auto 20px;
      }

      @include mq(xs) {
        width: 100%;
        max-width: 400px;
      }

      span {
        font-family: $fd, sans-serif;
      }

      &:nth-child(1) {
        margin-left: 140px;

        @include mq(lg) {
          margin-left: 80px;
        }

        @include mq(md) {
          margin-left: 0;
        }

        @include mq(s) {
          margin: 0 auto 20px;
        }
      }

      &:nth-child(2) {
        position: absolute;
        top: -13px;
        left: 625px;

        @include mq(lg) {
          left: 465px;
        }

        @include mq(md) {
          left: 50%;
        }

        @include mq(s) {
          position: static;
        }
      }

      &:nth-child(3) {
        position: absolute;
        top: -221px;
        right: 25px;

        @include mq(md) {
          right: 0;
        }

        @include mq(s) {
          position: static;
          margin: 0 auto;
        }
      }
    }
  }

  .dandelions {
    width: 23%;
    top: -147px;
    right: 0;
    z-index: 7;

    img {
      width: 100%;
    }
  }
}