/*
    footer style
 */

.footer {
  padding: 183px 0 20px;
  background: #fff;
  position: relative;
  z-index: 9;

  @include mq(lg) {
    padding: 103px 0 20px;
    background: transparent;
  }

  @include mq(md) {
    padding: 0 0 20px;
  }

  .container {
    position: relative;
  }

  &__content {
    margin-left: 57%;

    @include mq(md) {
      margin-left: 0;
      display: flex;
      flex-direction: column;
    }

    p.logo {

      @include mq(md) {
        order: 1;
      }
    }

    h2 {
      font-family: $fbk, sans-serif;
      font-size: 36px;
      color: #666;
      line-height: 1.111;
      margin: 6px 0 10px;

      @include mq(md) {
        max-width: 365px;
        margin: 6px auto 10px;
        text-align: center;
        order: 2;
      }

      @include mq(s) {
        font-size: 30px;
      }
    }

    p.sale {

      @include mq(md) {
        text-align: center;
        order: 3;
      }
    }

    .price {
      margin: 2px 0 0 190px;

      @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        margin: 2px 0 0 185px;
      }

      @include mq(xlg) {
        margin: 2px 0 0 130px;
      }

      @include mq(lg) {
        margin: 2px 0 0 85px;
      }

      @include mq(md) {
        margin: -14px auto 15px;
        text-align: center;
        order: 4;
      }
    }

    &-img {
      position: absolute;
      top: -106px;
      left: -86px;

      @include mq(xlg) {
        width: 57%;
        left: -26px;
      }

      @include mq(lg) {
        width: 56%;
        top: 50px;
        left: 0;
      }

      @include mq(md) {
        position: static;
        width: 80%;
        max-width: 743px;
        margin: 0 auto;
        order: 5;
      }

      @include mq(s) {
        width: 100%;
      }

      img {
        width: 100%;
      }
    }

    p.stock {
      margin-left: 190px;

      @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        margin-left: 185px;
      }


      @include mq(xlg) {
        margin-left: 130px;
      }

      @include mq(lg) {
        margin-left: 85px;
      }

      @include mq(md) {
        order: 7;
        text-align: center;
        margin: 0 auto;
      }

      @include mq(xs) {
        width: 55%;
      }
    }

    a.order {
      margin: 20px 0 57px;
      position: relative;
      z-index: 9;

      @include mq(md) {
        order: 6;
        margin: 20px auto 10px;
      }
    }

    p.copyright {
      font-family: $fbk, sans-serif;
      font-size: 18px;
      color: #474646;
      margin-left: 145px;
      position: relative;
      z-index: 9;

      @include mq(xlg) {
        margin-left: 25px;
      }

      @include mq(md) {
        order: 8;
        margin: 25px auto 0;
        text-align: center;
      }
    }

    .grass {

      &.blue {

        &.left {
          width: 49px;
          top: 380px;
          left: -135px;

          @include mq(lg) {
            left: -35px;
          }

          @include mq(md) {
            top: 168px;
            left: calc(100% - 71px);
          }

          @include mq(xs) {
            top: 22%;
            left: calc(100% - 49px);
          }

          img {
            width: 100%;
          }
        }

        &.right {
          top: -2px;
          right: -113px;

          @include mq(md) {
            top: calc(100% - 110px);
            right: 0;
          }

          @include mq(s) {
            right: -50px;
          }

          @include mq(xs) {
            right: -131px;
          }
        }
      }

      &.red {
        top: -144px;
        left: 556px;

        @include mq(xlg) {
          left: 530px;
        }

        @include mq(lg) {
          top: -50px;
          left: 453px;
        }

        @include mq(md) {
          top: -55px;
          left: calc(100% - 61px);
        }
      }
    }

    .box {
      width: 64px;
      height: 64px;
      background: #adc965;
      top: -75px;
      right: -192px;

      @include mq(lg) {
        right: 0;
      }

      @include mq(md) {
        top: -43px;
        left: 0;
      }
    }
  }

  .leaf {
    width: 40%;
    position: absolute;
    bottom: 0;
    right: 0;

    @include mq(xlg) {
      right: -1%;
    }

    @include mq(lg) {
      display: none;
    }

    @include mq(md) {
      display: block;
      width: auto;
      max-width: 234px;
      left: -105px;
    }

    @include mq(s) {
      left: calc(-200px + 3%);
    }

    @include mq(xs) {
      left: calc(-219px + 4.6875%)
    }

    img {
      width: 100%;

      @include mq(md) {
        width: auto;
        margin: 0;
      }
    }
  }
}