/*
    choose style
 */

.choose {
  background: url("../img/advantages-bg.jpg") no-repeat center/cover;
  padding: 170px 0 49px;

  @include mq(lg) {
    padding: 100px 0 49px;
  }

  @include mq(md) {
    padding: 40px 0 0;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 9;

    @include mq(md) {
      flex-wrap: wrap;
      justify-content: center;
    }

    &-img {
      box-sizing: border-box;
      width: 60%;
      padding: 0 53px;
      display: flex;
      justify-content: space-between;

      @include mq(lg) {
        padding: 0 20px;
      }

      @include mq(md) {
        width: 100%;
        order: 2;
        margin-top: 45px;
      }

      @include mq(xs) {
        flex-wrap: wrap;
        justify-content: center;
        padding: 0;
        max-width: 290px;
        margin: 45px auto 0;
      }

      a {
        width: 150px;
        height: 150px;
        background: #fff;
        position: relative;

        @include mq(xs) {
          width: 90px;
          height: 90px;
        }

        img {
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
        }

        &:before {
          content: "";
          display: block;
          box-sizing: border-box;
          width: 100%;
          height: 100%;
          border: 3px solid #fff;
          position: absolute;
          top: 20px;
          left: -20px;
        }

        &:nth-child(1) {

          @include mq(xs) {
            margin-right: 58px;
          }

          img {
            top: -42px;

            @include mq(xs) {
              width: 90px;
            }
          }
        }

        &:nth-child(2) {

          img {
            top: -37px;

            @include mq(xs) {
              width: 96px;
            }
          }
        }

        &:nth-child(3) {

          @include mq(xs) {
            margin-top: 58px;
          }

          img {
            top: -28px;

            @include mq(xs) {
              width: 88px;
            }
          }
        }
      }
    }

    &-text {
      width: 43%;

      @include mq(lg) {
        width: 38%;
      }

      @include mq(md) {
        width: 100%;
        order: 1;
      }

      h3 {
        text-align: center;
      }

      p.sale {
        color: #ffec50;
        text-align: center;
        margin-top: 8px;
      }
    }
  }
}