/*
    review style
 */

.review {
  padding: 63px 0 26px;
  position: relative;

  @include mq(md) {
    padding: 50px 0 66px;
  }

  h4 {
    text-align: right;
    margin-right: 30px;

    @include mq(md) {
      margin-right: 0;
      text-align: center;
    }
  }

  &__content {
    margin-top: 23px;
    margin-left: 256px;
    margin-right: 110px;
    box-sizing: border-box;
    position: relative;
    z-index: 9;

    @include mq(lg) {
      margin-left: 56px;
      margin-right: 50px;
    }

    @include mq(md) {
      margin: 26px 25px 0;
    }

    @include mq(xs) {
      margin: 26px auto 0;
    }

    &-slider {

      &__item {

        section {
          width: 88%;

          @include mq(s) {
            width: 100%;
          }

          h5 {
            font-family: $fb, sans-serif;
            font-size: 20px;
            color: #ff8357;
            text-transform: uppercase;
          }

          p {
            font-family: $fbk, sans-serif;
            font-size: 20px;
            color: #666;
            margin-top: 10px;
          }
        }
      }
    }

    &-arrow {
      display: flex;
      justify-content: space-between;
      width: 113%;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);

      @include mq(s) {
        width: 100%;
        position: static;
        transform: none;
        margin-top: 15px;
      }

      .prev__arrow, .next__arrow {
        cursor: pointer;
        position: relative;

        @include mq(s) {
          position: static;
        }
      }

      .prev__arrow {
        top: 77px;
      }

      .next__arrow {
        transform: scale(-1, 1);
        top: 149px;
      }
    }
  }

  p.send {
    font-family: $fd, sans-serif;
    font-size: 20px;
    color: #adc965;
    text-align: center;
    transition: all 0.25s ease-in-out;
    margin-top: 50px;
    cursor: pointer;

    @include mq(s) {
      margin-top: 20px;
    }

    &:hover {
      text-decoration: underline;
    }
  }

  .boot {
    width: 41.6%;
    position: absolute;
    bottom: -62px;
    left: 0;

    @include mq(xxlg) {
      width: 32%;
    }

    @include mq(xlg) {
      width: 36%;
    }

    @include mq(lg) {
      bottom: -120px;
    }

    @include mq(md) {
      display: none;
    }

    img {
      width: 100%;
    }
  }
}