/*
    advantages style
 */

.advantages {
  background: url("../img/advantages-bg.jpg") no-repeat center/cover;
  padding: 91px 0 0;
  position: relative;

  @include mq(md) {
    padding: 41px 0 0;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 9;

    @include mq(lg) {
      align-items: center;
    }

    @include mq(md) {
      flex-wrap: wrap;
    }

    &-text {
      box-sizing: border-box;
      width: 50%;
      padding: 0 0 0 32px;
      margin-top: 25px;

      @include mq(xlg) {
        padding: 0;
      }

      @include mq(lg) {
        margin-top: 0;
        width: 48%;
      }

      @include mq(md) {
        width: 100%;
      }

      h3 {

        @include mq(lg) {

          br {
            display: none;
          }
        }
      }

      ul {
        position: relative;
        margin-top: 65px;
        padding: 5px 10px 5px 0;

        @include mq(md) {
          padding: 5px 0;
        }

        @include mq(s) {
          margin-top: 25px;
        }

        li {
          margin-bottom: 45px;
          position: relative;

          p {
            font-family: $fbk, sans-serif;
            font-size: 22px;
            color: #2a3411;
            position: relative;
            margin-left: 82px;

            @include mq(lg) {

              br {
                display: none;
              }
            }

            @include mq(xs) {
              margin-left: 75px;
            }

            span {
              font-family: $fd, sans-serif;
            }
          }

          &:before {
            content: "";
            display: block;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
          }

          &:nth-child(1) {

            &:before {
              width: 50px;
              height: 35px;
              background: url("../img/advantages/1.png") no-repeat center/cover;
              left: 10px;

              @include mq(xs) {
                left: calc(-4.6875% + 10px);
              }
            }
          }

          &:nth-child(2) {

            &:before {
              width: 44px;
              height: 50px;
              background: url("../img/advantages/2.png") no-repeat center/cover;
              left: 13px;

              @include mq(xs) {
                left: calc(-4.6875% + 13px);
              }
            }
          }

          &:nth-child(3) {
            margin-bottom: 0;

            &:before {
              width: 38px;
              height: 38px;
              background: url("../img/advantages/3.png") no-repeat center/cover;
              left: 16px;

              @include mq(xs) {
                left: calc(-4.6875% + 16px);
              }
            }
          }
        }

        &:before {
          content: "";
          display: block;
          width: 70px;
          height: 100%;
          position: absolute;
          background: #fff;
          top: 0;
          left: 0;

          @include mq(xs) {
            left: -4.6875%;
          }
        }
      }
    }

    &-video {
      box-sizing: border-box;
      width: 50%;
      padding: 20px 15px 15px;
      background: #fff;
      box-shadow: 0px 0px 9.5px 0.5px rgba(0, 0, 0, 0.1);

      @include mq(md) {
        width: 100%;
        margin-top: 40px;
      }

      section {

        figure {
          padding-bottom: 52.5%;

          img {
            width: 100%;
          }
        }

        p {
          font-family: $fd, sans-serif;
          font-size: 20px;
          color: #888d7d;
          text-transform: uppercase;
        }

        &:nth-child(2) {
          margin-top: 25px;
        }
      }
    }
  }

  .dandelions {
    width: 24%;
    max-width: 449px;
    top: 0;
    right: 0;

    @include mq(s) {
      width: auto;
      right: -375px;
    }

    img {
      width: 100%;
    }
  }
}