/*
    general style
 */

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

html {
  height: 100%;
}

body {
  line-height: 1.2;
  background: #fff;
  font-family: $fbk, sans-serif;
  font-size: 18px;
  color: #222;
}

.container {
  width: 1200px;
  margin: 0 auto;

  @include mq(xlg) {
    width: 1140px;
  }

  @include mq(lg) {
    width: 960px;
  }

  @include mq(md) {
    width: 720px;
  }

  @include mq(s) {
    width: 94%;
  }

  @include mq(xs) {
    width: 90.625%;
  }
}

a {
  display: block;
  text-decoration: none;
  text-align: center;

  &:active, &:focus {
    outline: none;
  }
}

img {
  display: block;
  margin: 0 auto;
  object-fit: cover;
}

p.logo {
  font-family: $fb, sans-serif;
  font-size: 30px;
  color: #ff8357;
  text-transform: uppercase;

  @include mq(md) {
    text-align: center;
  }
}

.price {

  span.new-price {
    font-family: $fb, sans-serif;
    font-size: 60px;
    color: #ff6161;

    @include mq(s) {
      font-size: 50px;
    }
  }

  span.old-price {
    font-family: $fd, sans-serif;
    font-size: 20px;
    color: #9b9b9b;
    display: inline-block;
    position: relative;
    margin-top: -5px;
    margin-left: calc(100% - 80px);

    @include mq(md) {
      text-align: center;
      margin: -5px auto 0;
    }

    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background: #9c9c9c;
      position: absolute;
      top: 55%;
      left: 0;
      transform: translateY(-50%);
    }
  }
}

p.sale {
  font-family: $fb, sans-serif;
  font-size: 48px;
  color: #fac172;
  text-transform: uppercase;

  @include mq(s) {
    font-size: 40px;
  }
}

p.stock {
  font-family: $fd, sans-serif;
  font-size: 18px;
  color: #666;
  text-transform: uppercase;
}

a.order {
  font-family: $fb, sans-serif;
  font-size: 24px;
  color: #fff;
  box-sizing: border-box;
  width: 100%;
  max-width: 250px;
  padding: 10px 0;
  background: #ffb144;
  border: 1px solid #ffb144;
  transition: all 0.55s ease-in-out;

  &:hover {
    color: #ffb144;
    background: transparent;
  }
}

.leaf {
  position: absolute;
}

.grass {
  position: absolute;
}

.dandelions {
  position: absolute;
}

.spot {
  position: absolute;
}

h3 {
  font-family: $fd, sans-serif;
  font-size: 34px;
  color: #fff;

  @include mq(s) {
    font-size: 24px;
  }
}

.box {
  position: absolute;
}

h4 {
  font-family: $fd, sans-serif;
  font-size: 30px;
  color: #465626;
  text-transform: uppercase;

  @include mq(s) {
    font-size: 24px;
  }
}