/*
    header style
 */

.header {
  background: #fff;
  padding: 77px 0 65px;
  position: relative;

  @include mq(md) {
    padding: 22px 0 45px;
  }

  .container {
    position: relative;
  }

  nav {
    display: flex;
    justify-content: space-between;
    max-width: 360px;
    padding: 0 0 2px;
    margin-left: 153px;

    @include mq(xlg) {
      margin-left: 93px;
    }

    @include mq(md) {
      margin: 0 auto;
    }

    @include mq(s) {
      display: none;
    }

    a {
      font-family: $fm, sans-serif;
      font-size: 18px;
      color: #9c9c9c;
      position: relative;
      transition: all 0.25s ease-in-out;

      &:after {
        content: "";
        display: block;
        width: 100%;
        height: 1px;
        background: #9c9c9c;
        position: absolute;
        bottom: -2px;
        left: 0;
        opacity: 0;
        transition: inherit;
      }

      &:hover {
        color: #ff8357;

        &:after {
          opacity: 1;
          background: #ff8357;
        }
      }
    }
  }

  &__content {
    margin-left: 11px;
    margin-top: 90px;

    @include mq(xlg) {
      margin-left: 0;
    }

    @include mq(md) {
      margin-top: 25px;
    }

    @include mq(s) {
      margin-top: 0px;
    }

    h1 {
      font-family: $fbk, sans-serif;
      font-size: 36px;
      color: #666;
      line-height: 1.111;
      max-width: 365px;
      margin: 6px 0 5px;

      @include mq(md) {
        margin: 6px auto 5px;
        text-align: center;
      }

      @include mq(s) {
        font-size: 30px;
        margin: 0 auto;
      }

      @include mq(xs) {
        margin: 30px auto 0;
      }
    }

    p.sale {

      @include mq(md) {
        text-align: center;
      }
    }

    .price {
      margin: -18px 0 0 120px;

      @include mq(xlg) {
        margin: -18px 0 0 60px;
      }

      @include mq(md) {
        margin: -13px auto 0 ;
        text-align: center;
      }
    }

    p.stock {
      margin-left: 122px;

      @include mq(xlg) {
        margin-left: 62px;
      }

      @include mq(md) {
        margin: 0 auto;
        text-align: center;
      }
    }

    &-img {
      display: flex;
      justify-content: space-between;
      width: 100%;
      max-width: 650px;
      position: relative;
      position: absolute;
      top: -32px;
      right: 20px;
      z-index: 9;

      @include mq(xlg) {
        max-width: 590px;
      }

      @include mq(lg) {
        max-width: 440px;
        top: 75px;
      }

      @include mq(md) {
        position: relative;
        top: 0;
        left: 0;
        max-width: 610px;
        margin-left: auto;
        margin-top: 20px;
      }

      @include mq(s) {
        max-width: 320px;
        margin-right: 50px;
      }

      @include mq(xs) {
        width: 56%;
        margin-right: 0;
      }

      figure {
        position: relative;

        @include mq(xlg) {
          width: 49%;
        }

        @include mq(s) {
          width: 100%;
        }

        &.miniature {
          position: absolute;
          top: 100px;
          left: -125px;
          z-index: 9;

          @include mq(xlg) {
            width: auto;
          }

          @include mq(xs) {
            top: 0;
          }

          a {

            img {
              margin: 0;
              width: auto;
            }

            &:nth-child(2) {
              margin: -35px 0 0 -8px;
            }
          }
        }

        &.her {

          @include mq(s) {
            display: none;
          }
        }

        img {

          @include mq(xlg) {
            width: 100%;
          }
        }

        p {
          font-family: $fb, sans-serif;
          font-size: 48px;
          color: #fff;
          position: absolute;
          bottom: 75px;

          @include mq(xlg) {
            font-size: 42px;
          }

          @include mq(lg) {
            bottom: calc(100% - 75px);
          }

          @include mq(md) {
            font-size: 48px;
            bottom: 75px;
          }

          @include mq(s) {
            display: none;
          }
        }

        &.him {

          p {
            left: 15px;
          }
        }

        &.her {

          p {
            right: 12px;
          }
        }
      }

      &:before {
        content: "";
        display: block;
        width: 166px;
        height: 275px;
        background: #ff8357;
        position: absolute;
        top: 89px;
        left: -100px;

        @include mq(xs) {
          height: 82%;
          max-height: 275px;
          top: 30px;
          left: -128px;
        }
      }
    }

    a.order {
      margin-left: 249px;
      margin-top: 60px;

      @include mq(xlg) {
        margin-left: 189px;
      }

      @include mq(md) {
        margin: 20px auto 0;
      }
    }

    ul {
      display: flex;
      justify-content: space-between;
      max-width: 938px;
      margin: 85px auto 0;

      @include mq(md) {
        margin: 28px auto 0;
      }

      @include mq(s) {
        text-align: center;
      }

      @include mq(xs) {
        flex-wrap: wrap;
        justify-content: center;
      }

      li {
        font-family: $fd, sans-serif;
        font-size: 20px;
        color: #444;
        line-height: 1.08;
        position: relative;

        @include mq(md) {
          font-size: 18px;
        }

        @include mq(s) {
          font-size: 20px;
        }

        @include mq(xs) {
          width: 100%;
          margin-bottom: 35px;

          &:nth-child(4) {
            margin-bottom: 0;
          }
        }

        &:after {
          content: "";
          display: block;
          width: 15px;
          height: 15px;
          background: #ff8357;
          border-radius: 50%;
          position: absolute;
          top: 50%;
          right: -47px;
          transform: translateY(-50%);

          @include mq(md) {
            display: none;
          }
        }
      }
    }
  }

  .leaf {

    &.left {
      top: -77px;
      left: -360px;

      @include mq(md) {
        display: none;
      }
    }

    &.right {
      bottom: 80px;
      right: -155px;

      @include mq(md) {
        bottom: 20px;
        left: 0;
        right: 100%;
      }

      @include mq(s) {
        bottom: 40px;
      }

      @include mq(xs) {
        left: -76px;
      }
    }
  }

  .grass {

    &.blue {

      &.top {
        width: 49px;
        top: -4px;
        left: -11px;

        img {
          width: 100%;
        }

        @include mq(md) {
          display: none;
        }
      }

      &.left {
        width: 155px;
        top: 424px;
        left: -71px;

        @include mq(lg) {
          left: 0;
        }

        @include mq(md) {
          display: none;
        }

        img {
          width: 100%;
        }
      }

      &.right {
        top: 97px;
        right: -173px;

        @include mq(lg) {
          display: none;
        }

        @include mq(md) {
          display: block;
          top: -30px;
          right: -24px;
        }

        @include mq(s) {
          right: -131px;
        }

        @include mq(xs) {
          right: -141px;
        }
      }
    }

    &.red {

      &.left {
        top: 300px;
        left: -172px;

        @include mq(lg) {
          display: none;
        }

        @include mq(md) {
          display: block;
          top: -22px;
          left: 0;
        }

        @include mq(xs) {
          left: calc(-4.6875% - 25px);
        }
      }

      &.right {
        width: 59px;
        top: -71px;
        right: -34px;

        @include mq(lg) {
          right: 0;
        }

        @include mq(md) {
          display: none;
        }

        img {
          width: 100%;
        }
      }
    }
  }

  .box {

    &.left {
      width: 64px;
      height: 64px;
      background: #adc965;
      top: 204px;
      left: -103px;

      @include mq(md) {
        display: none;
      }
    }

    &.right {
      width: 166px;
      height: 275px;
      background: #adc965;
      top: 288px;
      right: -29px;

      @include mq(md) {
        display: none;
      }

      @include mq(s) {
        display: block;
        width: 64px;
        height: 64px;
        top: calc(100% - 115px);
        right: -39px;
      }

      @include mq(xs) {
        top: calc(100% - 140px);
      }
    }
  }

  &:after {
    content: "";
    display: block;
    width: 100%;
    height: 20px;
    background: linear-gradient(to right, #89d5c9, #ff8357 10%, #adc965 20%, #89d5c9 30%, #ff8357 40%, #adc965 50%, #89d5c9 60%, #adc965 70%, #89d5c9 80%, #ff8357 90%, #adc965);;
    box-shadow: 0 5px 13px 2px rgba(0, 0, 0, .15);
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 11;
  }
}