/*
    catalog style
 */

.catalog {
  background: url("../img/advantages-bg.jpg") center;
  padding: 90px 0 80px;
  position: relative;

  @include mq(md) {
    padding: 73px 0 32px;
  }

  &__block {
    box-sizing: border-box;
    background: #fff;
    padding: 31px 18px;
    margin-bottom: 80px;
    position: relative;
    z-index: 9;

    @include mq(md) {
      margin-bottom: 50px;
    }

    @include mq(s) {
      margin-bottom: 30px;
      padding: 15px 10px 25px;
    }

    h4 {

      @include mq(md) {
        text-align: center;
      }
    }

    .price {
      display: inline-block;
      position: absolute;
      top: 26px;
      left: 440px;

      @include mq(lg) {
        left: 360px;
      }

      @include mq(md) {
        position: static;
        margin: 0 auto;
        display: block;
        text-align: center;
      }

      span.new-price {
        display: block;
        font-size: 36px;
      }
    }

    ul {
      margin: 11px 0 62px;

      @include mq(md) {
        max-width: 395px;
        margin: 11px auto 32px;
      }

      li {
        line-height: 1.667;

        span {
          font-family: $fbk, sans-serif;
          font-size: 18px;
          color: #666;

          &:nth-child(1) {
            font-family: $fd, sans-serif;
            display: inline-block;
            width: 190px;

            @include mq(xs) {
              width: auto;
              margin-right: 5px;
            }
          }
        }
      }
    }

    &-img {
      width: 530px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      position: absolute;
      top: 20px;
      right: 20px;

      @include mq(xlg) {
        width: 44%;
      }

      @include mq(md) {
        position: static;
        width: 80%;
        margin: 0 auto;
      }

      @include mq(s) {
        width: 95%;
      }

      @include mq(xs) {
        width: 100%;
      }

      a {
        box-sizing: border-box;
        width: 49%;
        max-width: 258px;
        border: 1px solid #9c9c9c;
        margin-bottom: 10px;

        @include mq(s) {
          max-width: 100%;
        }

        img {
          width: 100%;
        }
      }
    }

    .size {
      display: flex;
      justify-content: space-between;
      max-width: 492px;

      @include mq(md) {
        margin: 0 auto;
      }

      @include mq(xs) {
        flex-wrap: wrap;
        justify-content: center;
      }

      p {
        font-family: $fd, sans-serif;
        font-size: 18px;
        color: #666;
        max-width: 180px;
        line-height: 1.667;

        @include mq(xs) {
          max-width: 100%;
          width: 100%;
        }
      }

      figure {
        display: flex;
        flex-wrap: wrap;
        max-width: 312px;

        @include mq(xs) {
          justify-content: center;
        }

        span {
          font-family: $fbk, sans-serif;
          font-size: 18px;
          color: #666;
          text-align: center;
          display: block;
          box-sizing: border-box;
          width: 104px;
          padding: 5px 0;
          transition: all 0.55s ease-in-out;
          cursor: pointer;

          &.activeSize {
            background: #99b859;
          }
        }
      }
    }

    form {
      width: 100%;
      max-width: 460px;
      margin: 32px 0 35px 2px;
      display: flex;
      justify-content: space-between;
      position: relative;

      @include mq(md) {
        margin: 25px auto 0;
        flex-wrap: wrap;
      }

      .form-group {
        width: 48%;

        @include mq(xs) {
          width: 100%;
        }

        input {
          font-family: $fbk, sans-serif;
          font-size: 16px;
          color: #888;
          box-sizing: border-box;
          display: block;
          width: 100%;
          padding: 9.5px 20px;
          background: #e2e2e2;
          border: none;

          @include mq(xs) {
            max-width: 280px;
            margin: 0 auto 10px;
          }

          &::placeholder {
            font-family: $fbk, sans-serif;
            font-size: 16px;
            color: #888;
            opacity: 1;
          }

          &:required {
            outline: none;
          }
        }

        &:nth-child(3) {
          width: 54.5%;
          position: absolute;
          bottom: -64px;
          right: -115px;

          @include mq(md) {
            width: 100%;
            position: static;
          }

          button {
            font-family: $fb, sans-serif;
            font-size: 24px;
            color: #fff;
            display: block;
            box-sizing: border-box;
            width: 100%;
            max-width: 250px;
            padding: 6px 0;
            background: #ffb144;
            border: 1px solid #ffb144;
            transition: all 0.55s ease-in-out;
            cursor: pointer;

            @include mq(md) {
              margin: 30px auto 20px;
            }

            @include mq(xs) {
              max-width: 280px;
            }

            &:hover {
              color: #ffb144;
              background: transparent;
            }
          }
        }
      }
    }

    p.stock {
      margin-left: 12px;
      color: #ff5050;

      @include mq(md) {
        margin: 0 auto;
        text-align: center;
      }
    }

    &.lake {

      .price {

        @include mq(lg) {
          position: static;
        }
      }
    }

    &.vintage {
      margin-bottom: 0;

      ul {
        margin: 11px 0 46px;

        @include mq(md) {
          margin: 11px auto 32px;
        }
      }

      >p {
        font-family: $fbk, sans-serif;
        font-size: 18px;
        color: #666;
        line-height: 1.667;
        max-width: 508px;

        @include mq(lg) {
          max-width: 493px;
        }

        @include mq(md) {
          max-width: 100%;
          margin: 0 auto;
          text-align: center;
        }
      }

      >span {
        font-family: $fb, sans-serif;
        font-size: 16px;
        color: #465626;
        line-height: 1.875;
        text-transform: uppercase;
        display: block;
        margin: 2px 0 40px;

        @include mq(md) {
          text-align: center;
        }

        @include mq(xs) {

          br {
            display: none;
          }
        }
      }

      p.stock {
        font-family: $fd, sans-serif;
        color: #ff5050;
      }
    }
  }

  &:before {
    content: "";
    display: block;
    width: 77%;
    height: 80%;
    background: #ff8357;
    position: absolute;
    top: 190px;
    left: 50%;
    transform: translateX(-50%);
    filter: blur(150px);

    @include mq(xxlg) {
      width: 90%;
    }

    @include mq(lg) {
      width: 100%;
    }

    @include mq(md) {
      display: none;
    }
  }
}